import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { LoginCheckService } from 'src/app/api/controllers/LoginCheck';
import { SharedService } from '../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {

  userPassword:string = "";
  userConfirmPassword:string = "";

  isPasswordMatch:boolean = false;

  isSubmitted:boolean = true;

  user:any;
  genderType:string = null;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
  	private userService:UserService,
  	private loginCheckService:LoginCheckService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  logout(){
    this.sharedService.logout();
  }

  submitResetPasswordForm(form){

    this.isSubmitted = false;

  	if(this.userPassword != this.userConfirmPassword){
  		this.isPasswordMatch = true
  	}
  	else {

  		let details = { 
  		  "userId": parseInt(this.cookieService.get('userId')),
  		  "password": this.userPassword,
  		  "updateBy": parseInt(this.cookieService.get('userId'))
      }
      
      let params = {
        AdminUsers_vm: details
      }

  		this.loginCheckService.changePassword(params).subscribe((res:any) => {
        //update user
        var userDetails = {
          "userId": this.route.params['value'].id,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "firstName": this.user.firstName,
          "middleName": "",
          "lastName": this.user.lastName,
          "emailId": this.user.emailId,
          "password": this.user.password,
          "salt": "",
          "address1": "",
          "address2": "",
          "city": "",
          "state": "",
          "country": "",
          "zipCode": "",
          "genderId": parseInt(this.genderType),
          "dob": "2019-11-10T09:58:08.934Z",
          "joinedOn": "2019-11-10T09:58:08.934Z",
          "bloodGroup": "",
          "phoneNumber": this.user.phoneNumber.toString(),
          "emergencyContactNumber": "",
          "emergencyContactPerson": "",
          "emergencyContactNumberSecondary": "",
          "emergencyContactPersonSecondary": "",
          "accessCardNumber": "",
          "notes": "",
          "profilePictureId": null,
          "isLiving": true,
          "isActive": true,
          "isApproved": this.user.isApproved,
          "resetPassword": true, // updated reset password
          "insertedBy": 0,
          "updatedBy": parseInt(this.cookieService.get('userId'))
        }

        let userParams = {
          user: userDetails
        }

        this.userService.updateUser(userParams).subscribe((res:any) => {
           if(res.message) {
            this.isSubmitted = true;
            this.sharedService.setAlertMessage("Password has been reset successfully");
            // this.router.navigateByUrl('/ams');
            var userRole = this.cookieService.get('userRole');
            if(userRole == 'Admin' || userRole == 'SuperAdmin' || userRole == 'Staff' || userRole == 'Security'){
              this.router.navigateByUrl('/ams');
            }
            else if(userRole == 'Owner' || userRole == 'Tenant') {
              this.router.navigateByUrl('/user');
            }
            else {
      
            }
           }
        });


  		},
  		error => {

  		});

  	}

  }


  clearErrors() {
  	
  }

  ngOnInit() {
    let getUserParam ={
      userid:this.route.params['value'].id
    }
    this.userService.getUserById(getUserParam).subscribe((res:any) => {

        this.user = res[0];
        this.genderType = this.user.genderId.toString();

   });

  }

}
